.a{
    height: 100vh;
    display: flex;
    align-items: center;
}
.a-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;

}
.a-right{
    flex:1;
    flex-direction: column;
}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color:#59b256;
}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}
.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.a-award-img{
    width: 150px;
    height: 130;
    border-radius: 20px;
    margin-top: 70px;
}

.a-title{
    font-weight: 400;

}
.a-sub{margin:20px 0px;}
.a-desc{
    font-weight: 300;
}
.a-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;

    

}
.a-award-texts{
    width: 75%;
}
.a-award-title{
    font-weight: bold;
    color:#555;
    margin-bottom: 10px;
}
